									<div>
										<p class="line-through text-xs text-end">
											IDR {adultCount * selectedTicket?.adult_price +
												childrenCount * selectedTicket?.children_price +
												infantCount * selectedTicket?.infant_price}
										</p>
										<p class="text-blue font-bold text-lg">
											IDR {(adultCount * selectedTicket?.adult_price +
												childrenCount * selectedTicket?.children_price +
												infantCount * selectedTicket?.infant_price) *
												(1 - selectedTicket?.discount_percentage / 100)}
										</p>
									</div>
								</div>
								<form on:submit={handleSubmit}>
									<input type="hidden" name="id" bind:value={selectedTicket.id} />
									<input type="hidden" name="name" bind:value={selectedTicket.name} />
									<input type="hidden" name="slug" bind:value={selectedTicket.slug} />
									<input type="hidden" name="adults" bind:value={adultCount} />
									<input type="hidden" name="children" bind:value={childrenCount} />
									<input type="hidden" name="infants" bind:value={infantCount} />
									<input type="hidden" name="date" bind:value={date} />
									<input type="hidden" name="adult_price" bind:value={selectedTicket.adult_price} />
									<input
										type="hidden"
										name="infant_price"
										bind:value={selectedTicket.infant_price}
									/>
									<input
										type="hidden"
										name="chlidren_price"
										bind:value={selectedTicket.children_price}
									/>
									<input
										type="hidden"
										name="total_price"
										value={(adultCount * selectedTicket?.adult_price +
											childrenCount * selectedTicket?.children_price +
											infantCount * selectedTicket?.infant_price) *
											(1 - (selectedTicket?.discount_percentage || 0) / 100).toFixed(2)}
									/>

									<button
										type="submit"
										class="text-white w-full mt-5 p-2.5 text-sm rounded-lg bg-blue font-bold text-center"
										>Book Ticket</button
									>
								</form>
